.contact-container {
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
    grid-template-rows: .6fr 2fr .2fr;

    font-family: 'chunk';
    color: $color-dark-text;

    //align-items: stretch;

    .form-page-title {
        display: inherit;
        grid-column: 2;
        place-self: center;
        font-size: 30px;
        

    }

    .form-container {
        display: inherit;
        grid-template-columns: 1fr;
        grid-column: 2;
        grid-row: 2;

        //align-items: start;
        //justify-items: center;

        place-items: stretch;

        background-color: $color-orange;
        border-radius: 20px;

        padding: 20px;
    }

    .form-title {
        font-size: 23px;
        padding-bottom: 10px;
    }

    .form-field {
        border-radius: 4px;
        border: none;
        font-size: 15px;
        padding-bottom: 30px;
        width: 50vw;

        vertical-align:bottom;
    }

    .form-button {
        background-color: $color-dark-text;
        color: $color-white-paper;
        border: none;
        height: 4vh;
        width: 6vw;
        border-radius: 4px;
        margin-top: 20px;
    }
    
}