$counter: url('../images/counters.JPG');
$bath-before: url('../images/bathroom\ before.JPG');
$bath-after: url('../images/bathroom\ after.JPG');
$patio: url('../images/patio.JPG');

.works-container {
    display: inherit;

    grid-template-rows: 16vh 45vh 60vh 40vh 8vh;

    grid-template-columns: 1fr 5fr 1fr;

    overflow-y: scroll !important;

    .title-container {
        display: inherit;
        grid-row: 1;
        grid-column: 2;

        align-self: center;

        font-family: 'chunk';
        font-size: 30px;
    }

    row-gap: 40px;

    

    .show-1{
        border-radius: 7px;
        display: inherit;
        grid-row: 2;
        grid-column: 2;
        background-color: $color-orange;

        grid-template-columns: 3fr 1fr;

        .show-1-image {
            display: inherit;
            grid-column: 1;
            background-image: $counter;
            border-radius: 7px 0 0 7px;
            background-size: cover;
        }

        .show-1-text {
            display: inherit;
            grid-column: 2;

            padding: 20px;
            font-family: 'chunk';
            font-size: 30px;

            place-self: center;
        }

    }
    .show-2 {
        border-radius: 7px;
        display: inherit;
        grid-row: 3;
        grid-column: 2;
        background-color: $color-orange;

        grid-template-columns: 2fr 1fr;
        grid-template-rows: 1fr 2fr;

        .show-2-text {
            display: inherit;
            grid-column: 1;
            grid-row: 1;

            padding: 20px;
            font-family: 'chunk';
            font-size: 30px;

            place-self: center;
        }

        .show-2-image-1 {
            display: inherit;
            grid-row: 2;
            grid-column: 1;
            background-image: $bath-before;
            background-size: cover;
            object-position: top;
            border-radius: 0 0 0 7px;

            //scale: .5;
            //max-width: 400px;
            //height: auto;
        }
        .show-2-image-2 {
            display: inherit;
            grid-column: 2;
            grid-row-start: 1;
            grid-row-end: 3;
            background-image: $bath-after;
            background-size: cover;
            border-radius: 0 7px 7px 0;
            


        }

    }
    .show-3 {
        border-radius: 7px;
        display: inherit;
        grid-row: 4;
        grid-column: 2;
        background-color: $color-orange;

        grid-template-columns: 1fr 3fr;

        .show-3-text {
            display: inherit;
            grid-column: 1;

            padding: 20px;
            font-family: 'chunk';
            font-size: 30px;
            place-self: center;

        }

        .show-3-image {
            display: inherit;
            grid-column: 2;
            background-image: $patio;
            background-size: cover;
            border-radius: 0 7px 7px 0; 
        }
    }

    .additional-services {
        display: inherit;
        grid-column: 2;
        grid-row: 5;

        padding-bottom: 20px;

        .title {
            
            font-family: 'chunk';
            font-size: 20px;
            padding-bottom: 10px;
        }
        .bullet-points {
            line-height: 25px;
        }
    }
}