.about-container {
    display: inherit;
    grid-template-columns: 1fr 3fr 1fr;
    grid-template-rows: .3fr 3fr .5fr;

    .info-container {
        display: inherit;
        grid-column: 2;
        grid-row: 2;

        grid-template-rows: 1fr 3fr 1fr;

        .title-container {
            display: inherit;
            grid-row: 1;

            .title {
                display: inherit;
                align-self: end;
                font-family: 'chunk';
                font-size: 30px;
            }

            .subtitle {
                display: inherit;
                align-self: start;
                font-family: 'Montserrat';
            }
        }

        .body {
            display: inherit;
            grid-row: 2;

            .paragraph-text {
                font-family: 'Montserrat';
            }

            .subtitle-text {
                font-family: 'chunk';
                font-size: 20px;
            }

            .bullet-points {
                font-family: 'Montserrat';
                line-height: 25px;
            }
        }

        .contact {
            display: inherit;
            grid-row: 3;

            .subtitle-text {
                font-family: 'chunk';
                font-size: 20px;
            }

            .bullet-points {
                font-family: 'Montserrat';
                line-height: 25px;
            }

            .phone-number {
                text-decoration: none;
                color: $color-dark-text;
            }
        }

    }
}