@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap");
@font-face {
  font-family: "chunk";
  src: local("ChunkFive-Regular"), url(../fonts/ChunkFive-Regular.woff) format("woff");
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.App {
  height: 100vh;
  background-color: #fff7f0;
}

.link {
  text-decoration: none;
  color: #200000;
  justify-self: center;
  font-family: "Montserrat";
}

.link-h {
  text-decoration: none;
  color: #200000;
  font-family: "Montserrat";
  font-weight: bold;
}

.contact-container {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  grid-template-rows: 0.6fr 2fr 0.2fr;
  font-family: "chunk";
  color: #200000;
}
.contact-container .form-page-title {
  display: inherit;
  grid-column: 2;
  place-self: center;
  font-size: 30px;
}
.contact-container .form-container {
  display: inherit;
  grid-template-columns: 1fr;
  grid-column: 2;
  grid-row: 2;
  place-items: stretch;
  background-color: #ff8864;
  border-radius: 20px;
  padding: 20px;
}
.contact-container .form-title {
  font-size: 23px;
  padding-bottom: 10px;
}
.contact-container .form-field {
  border-radius: 4px;
  border: none;
  font-size: 15px;
  padding-bottom: 30px;
  width: 50vw;
  vertical-align: bottom;
}
.contact-container .form-button {
  background-color: #200000;
  color: #fff7f0;
  border: none;
  height: 4vh;
  width: 6vw;
  border-radius: 4px;
  margin-top: 20px;
}

.homepage {
  display: inherit;
  grid-template-columns: 1fr 5fr 1fr;
  grid-template-rows: 0.6fr 4fr 2fr 1fr;
  row-gap: 30px;
}

.home-hero {
  display: inherit;
  grid-column: 2;
  grid-row: 2;
  background-color: #ff8864;
  grid-template-columns: 1fr 1fr;
  padding-left: 70px;
  border-radius: 7px;
}
.home-hero .hero-info {
  display: inherit;
  grid-template-rows: 1.6fr 1fr 1fr;
}
.home-hero .hero-info .hero-title {
  display: inherit;
  grid-row: 1;
  align-self: end;
  font-family: "chunk";
}
.home-hero .hero-info .hero-sub {
  display: inherit;
  grid-row: 2;
  align-self: center;
  font-family: "Montserrat";
}
.home-hero .hero-info .hero-button {
  transition: all 0.3s;
  display: inherit;
  text-decoration: none;
  background-color: #fff7f0;
  justify-self: start;
  border-radius: 7px;
  height: 5vh;
  width: 11vw;
}
.home-hero .hero-info .hero-button .button-text {
  display: inherit;
  place-self: center;
  font-family: "Montserrat";
  color: #200000;
}
.home-hero .hero-info .hero-button:hover {
  background-color: #7fd1ae;
  border: solid 1px black;
}
.home-hero .hero-graphic {
  display: inherit;
  place-items: center;
}
.home-hero .hero-graphic .hero-logo {
  height: 200px;
  scale: 2;
  fill: #200000;
}

.home-categories {
  display: inherit;
  grid-row: 3;
  grid-column: 2;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  column-gap: 20px;
  justify-items: space-between;
}
.home-categories .cat-box {
  transition: all 0.2s;
  display: inherit;
  background-color: #ff8864;
  border-radius: 7px;
  font-family: "chunk";
  text-decoration: none;
  color: #200000;
  font-size: 22px;
  padding: 10px;
  text-align: center;
}
.home-categories .cat-box .cat-text {
  display: inherit;
  place-self: center;
}
.home-categories .cat-box:hover {
  background-color: #7fd1ae;
  border: solid 1px black;
}

.about-container {
  display: inherit;
  grid-template-columns: 1fr 3fr 1fr;
  grid-template-rows: 0.3fr 3fr 0.5fr;
}
.about-container .info-container {
  display: inherit;
  grid-column: 2;
  grid-row: 2;
  grid-template-rows: 1fr 3fr 1fr;
}
.about-container .info-container .title-container {
  display: inherit;
  grid-row: 1;
}
.about-container .info-container .title-container .title {
  display: inherit;
  align-self: end;
  font-family: "chunk";
  font-size: 30px;
}
.about-container .info-container .title-container .subtitle {
  display: inherit;
  align-self: start;
  font-family: "Montserrat";
}
.about-container .info-container .body {
  display: inherit;
  grid-row: 2;
}
.about-container .info-container .body .paragraph-text {
  font-family: "Montserrat";
}
.about-container .info-container .body .subtitle-text {
  font-family: "chunk";
  font-size: 20px;
}
.about-container .info-container .body .bullet-points {
  font-family: "Montserrat";
  line-height: 25px;
}
.about-container .info-container .contact {
  display: inherit;
  grid-row: 3;
}
.about-container .info-container .contact .subtitle-text {
  font-family: "chunk";
  font-size: 20px;
}
.about-container .info-container .contact .bullet-points {
  font-family: "Montserrat";
  line-height: 25px;
}
.about-container .info-container .contact .phone-number {
  text-decoration: none;
  color: #200000;
}

.works-container {
  display: inherit;
  grid-template-rows: 16vh 45vh 60vh 40vh 8vh;
  grid-template-columns: 1fr 5fr 1fr;
  overflow-y: scroll !important;
  row-gap: 40px;
}
.works-container .title-container {
  display: inherit;
  grid-row: 1;
  grid-column: 2;
  align-self: center;
  font-family: "chunk";
  font-size: 30px;
}
.works-container .show-1 {
  border-radius: 7px;
  display: inherit;
  grid-row: 2;
  grid-column: 2;
  background-color: #ff8864;
  grid-template-columns: 3fr 1fr;
}
.works-container .show-1 .show-1-image {
  display: inherit;
  grid-column: 1;
  background-image: url("../images/counters.JPG");
  border-radius: 7px 0 0 7px;
  background-size: cover;
}
.works-container .show-1 .show-1-text {
  display: inherit;
  grid-column: 2;
  padding: 20px;
  font-family: "chunk";
  font-size: 30px;
  place-self: center;
}
.works-container .show-2 {
  border-radius: 7px;
  display: inherit;
  grid-row: 3;
  grid-column: 2;
  background-color: #ff8864;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: 1fr 2fr;
}
.works-container .show-2 .show-2-text {
  display: inherit;
  grid-column: 1;
  grid-row: 1;
  padding: 20px;
  font-family: "chunk";
  font-size: 30px;
  place-self: center;
}
.works-container .show-2 .show-2-image-1 {
  display: inherit;
  grid-row: 2;
  grid-column: 1;
  background-image: url("../images/bathroom before.JPG");
  background-size: cover;
  object-position: top;
  border-radius: 0 0 0 7px;
}
.works-container .show-2 .show-2-image-2 {
  display: inherit;
  grid-column: 2;
  grid-row-start: 1;
  grid-row-end: 3;
  background-image: url("../images/bathroom after.JPG");
  background-size: cover;
  border-radius: 0 7px 7px 0;
}
.works-container .show-3 {
  border-radius: 7px;
  display: inherit;
  grid-row: 4;
  grid-column: 2;
  background-color: #ff8864;
  grid-template-columns: 1fr 3fr;
}
.works-container .show-3 .show-3-text {
  display: inherit;
  grid-column: 1;
  padding: 20px;
  font-family: "chunk";
  font-size: 30px;
  place-self: center;
}
.works-container .show-3 .show-3-image {
  display: inherit;
  grid-column: 2;
  background-image: url("../images/patio.JPG");
  background-size: cover;
  border-radius: 0 7px 7px 0;
}
.works-container .additional-services {
  display: inherit;
  grid-column: 2;
  grid-row: 5;
  padding-bottom: 20px;
}
.works-container .additional-services .title {
  font-family: "chunk";
  font-size: 20px;
  padding-bottom: 10px;
}
.works-container .additional-services .bullet-points {
  line-height: 25px;
}

.header-container {
  background-color: #fff7f0;
  height: 6vh;
  display: grid;
  border-bottom: solid 1px;
  border-color: #200000;
  grid-template-columns: 1fr 2fr 1fr;
}
.header-container .logo-container {
  display: inherit;
  align-items: center;
  padding-left: 25px;
}
.header-container .links-container {
  display: inherit;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  align-items: center;
}

.content-container {
  height: 94vh;
  display: grid;
  align-items: stretch;
}

