.homepage {
    display: inherit;

    grid-template-columns: 1fr 5fr 1fr;
    grid-template-rows: .6fr 4fr 2fr 1fr;

    row-gap: 30px;
}

.home-hero {
    display: inherit;
    grid-column: 2;
    grid-row: 2;
    background-color: $color-orange;

    grid-template-columns: 1fr 1fr;
    padding-left: 70px;

    border-radius: 7px;

    .hero-info {
        display: inherit;

        grid-template-rows: 1.6fr 1fr 1fr;

        .hero-title {
            display: inherit;
            grid-row: 1;
            align-self: end;
            font-family: "chunk";
        }

        .hero-sub {
            display: inherit;
            grid-row: 2;
            align-self: center;
            font-family: "Montserrat";
        }

        .hero-button {
            transition: all .3s;
            display: inherit;
            text-decoration: none;
            background-color: $color-white-paper;
            justify-self: start;
            //align-self: center;
            border-radius: 7px;
            height: 5vh;
            width: 11vw;

            .button-text {
                display: inherit;
                place-self: center;
                font-family: 'Montserrat';
                color: $color-dark-text;
            }
        }

        .hero-button:hover {
            background-color: $color-green;
            border: solid 1px black;
        }
    }

    .hero-graphic {
        display: inherit;
        place-items: center;
        .hero-logo {
            height: 200px;
            scale: 2;
            fill: $color-dark-text;
        }
    }
}

.home-categories {
    display: inherit;
    grid-row: 3;
    grid-column: 2;

    grid-template-columns: 1fr 1fr 1fr 1fr;
    column-gap: 20px;

    justify-items: space-between;

    .cat-box {
        transition: all .2s;
        display: inherit;
        background-color: $color-orange;
        border-radius: 7px;
        font-family: 'chunk';
        text-decoration: none;
        color: $color-dark-text;
        font-size: 22px;
        padding: 10px;
        text-align: center;
        //place-self: stretch;  
        .cat-text{
            display: inherit;
            place-self: center;
        }
    }

    .cat-box:hover {
        background-color: $color-green;
        border: solid 1px black;
    }
}
