.header-container {
    background-color: $color-white-paper;
    height: 6vh;

    display: grid;

    border-bottom: solid 1px;
    border-color: $color-dark-text;

    grid-template-columns: 1fr 2fr 1fr;

    .logo-container {
        display: inherit;
        align-items: center;
        padding-left: 25px;
        
    }

    

    .links-container {
        display: inherit;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        align-items: center;
    }
    
}

.content-container {
    height: 94vh;
    display: grid;

    align-items: stretch;
}
